.contacts {
  margin-bottom: 40px;
}

.contacts__title {
  padding: 0;
  margin: 0 0 34px 0;
  color: $c-main;
}

.contacts__box {
  padding-bottom: 18px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contacts__box:last-child {
  margin-bottom: 17px;
}

.contacts__text {
  line-height: 25px;
  color: $c-main;
}

.contacts__link {
  font-family: $ff-main;
  font-size: 24px;
  line-height: 33px;
  font-weight: 300;
  text-align: center;
  color: $base-pink;
}

.contacts__triangle {
  position: relative;
}

.contacts__triangle-svg {
  position: absolute;
  top: -31px;
  right: 0;
}

@media (min-width: $tablet-width) {
  .contacts__title {
    font-size: 40px;
    line-height: 54px;
  }

  .contacts__wrap {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .contacts__box:last-child {
    grid-column: 1/3;
  }

  .contacts__box:last-child .contacts__link {
    display: inline-block;
    width: 211px;
  }
}

@media (min-width: $desktop-width) {
  .contacts__wrap {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: flex-start;
  }

  .contacts__box:nth-child(2) {
    grid-column: 3/4;
  }

  .contacts__box:last-child {
    grid-column: 2/3;
    grid-row: 1/2;
  }
}
